@import '../../variables.scss';

.detailsItem {
  display: flex;
  width: fit-content;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 8px 18px;
  margin: 0;
  border-radius: 20px;
  background-color: $white;

  .icon > svg {
    width: 20px;
    height: 20px;
  }
  .value {
    margin: 0 8px;
  }
}
